.name-box {
    display: flex;
    flex-direction: column;
}

.contactTheAuthor-form-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.name-label {
    align-self: start;
    color: white;
    margin-bottom: .2rem;
}

.name-input {
    padding: 1rem .7rem;
    border-radius: 5px;
    border: none;
    outline-color: #17285a;
}

.message-input {
    padding: 1rem .7rem;
    border-radius: 5px;
    border: none;
    min-width: inherit;
    height: 5rem;
    resize: none;
    outline-color: #17285a;
}

.statement-container-email {
    align-items: center;
    text-align: center;
    margin-bottom: 3rem;
}

.submit-button {
    padding: 1rem 3rem;
    border-radius: 5px;
    border: none;
    background-color: #17285a;
    color: white;
    box-shadow: 0 6px rgb(109, 109, 109)
}

.submit-button:hover {
    background-color: #131f48
}

.submit-button:active {
  background-color: #131f48;
  box-shadow: 0 3px rgb(80, 80, 80);
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}




